  .logo {
      text-align: center
  }
  
  .card {
      border: none;
      width: 400px;
      margin: auto;
      border-radius: 10px;
      box-shadow: 0 6px 15px rgba(0, 0, 0, .16)
  }
  
  .card .card-body {
      padding: 30px;
      
      
      
  }
  .my-bold-text {
    font-weight: bold;
  }
  
  .card{
    margin-left: -29px;
    
    
