  .form-group {
      margin-bottom: 20px
  }
  
  .form-group .form-icon-wrapper {
      position: relative
  }
  
  .form-group .form-icon-wrapper .form-icon-left,
  .form-group .form-icon-wrapper .form-icon-right {
      padding: 0;
      transform: translateY(-50%);
      top: 50%;
      font-size: 16px;
      border: none;
      position: absolute;
      z-index: 4;
      background: none;
      transition: opacity .3s;
      color: #000
  }
  
  .form-group .form-icon-wrapper .form-icon-left {
      left: 15px
  }
  
  .form-group .form-icon-wrapper .form-icon-right {
      right: 15px;
      left: auto
  }
  
  .form-control {
      height: auto;
      padding: 13px 20px 13px 45px;
      border: none;
      
  }
  
  .form-control:focus {
      box-shadow: none;
      color: #000;
      background: #ebebeb
  }
  
  .form-control:focus+i {
      opacity: .8
  }
  
  .social-links {
      display: flex
  }
  
  .social-links a {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 40px;
      font-size: 18px;
      color: #fff;
      height: 40px;
      background: hsla(0, 0%, 100%, .4);
      border-radius: 100px;
      margin-right: 15px;
      transition: all .3s
  }
  
  .social-links a:last-child {
      margin-right: 0
  }
  
  .social-links a:focus,
  .social-links a:hover {
      background: hsla(0, 0%, 100%, .8);
      transform: scale(1.1)
  }
  
